import React, { useState } from "react";
import DiningLayout from "../layouts/DiningLayout";
import "../css/Sign_up.css";

const Sign_up = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [modalContent, setModalContent] = useState("");
    const contextPath = "";

    const openModal = (content) => {
        setModalContent(content);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
        setModalContent("");
    };

    return (
        <DiningLayout>
            <form
                id="signUpForm"
                className="sign_up_form"
                action={`${contextPath}/sign_up`}
                method="post"
            >
                <p className="sign_up_title">회원가입</p>

                <input
                    type="text"
                    name="member_id"
                    className="input_sign_up"
                    placeholder="아이디"
                    required
                />
                <input
                    type="password"
                    name="member_pw"
                    className="input_sign_up"
                    placeholder="비밀번호"
                    required
                />
                <input
                    type="password"
                    name="member_pw_confirm"
                    className="input_sign_up"
                    placeholder="비밀번호 확인"
                    required
                />
                <input
                    type="text"
                    name="nickname"
                    className="input_sign_up"
                    placeholder="닉네임"
                    required
                />
                <input
                    type="email"
                    name="email"
                    className="input_sign_up"
                    placeholder="이메일"
                    required
                />

                <div className="email_agree">
                    <label>
                        <input
                            type="radio"
                            name="email_agree"
                            value="agree"
                            required
                        />{" "}
                        이메일 수신 동의
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="email_agree"
                            value="disagree"
                        />{" "}
                        이메일 수신 거부
                    </label>
                </div>

                <div className="terms">
                    <label>
                        <input type="checkbox" name="terms_agree" required />{" "}
                        이용약관 및{" "}
                        <span
                            className="link"
                            onClick={() =>
                                openModal("이용약관")
                            }
                        >
                            이용약관
                        </span>
                        과{" "}
                        <span
                            className="link"
                            onClick={() =>
                                openModal("이용약관")
                            }
                        >
                            이용약관
                        </span>
                    </label>
                </div>

                <button type="submit" id="signUpButton" className="btn_sign_up">
                    회원가입
                </button>
            </form>

            {isModalOpen && (
                <div className="modal">
                    <div className="modal_content">
                        <span className="modal_close" onClick={closeModal}>
                            &times;
                        </span>
                        {modalContent === "terms" ? (
                            <div>
                                <h2>이용약관</h2>
                                <p>
                                    여기에 이용약관 내용이 들어갑니다.
                                </p>
                            </div>
                        ) : (
                            <div>
                                <h2>개인정보 취급방침</h2>
                                <p>
                                    여기에 개인정보 취급방침 내용이 들어갑니다.
                                </p>
                            </div>
                        )}
                    </div>
                </div>
            )}
        </DiningLayout>
    );
};

export default Sign_up;
